* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Noto Sans KR", sans-serif;
}
body {
  color: #323d45;
  overflow: hidden;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
body::-webkit-scrollbar {
  display: none;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header {
  color: #fafafa;
  position: absolute;
  left: 0%;
  right: 0%;
  top: 0%;
  bottom: 0%;
  padding: 0px 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  min-height: 60px;
  background-color: #1565c0;
  box-shadow: none;
  border-bottom: 1px solid rgb(222, 226, 230);
  height: 70px;
}

.header_company {
  color: #fafafa;
  display: flex;
  align-items: center;
}
.header_company > h1,
span {
  margin: 0px 3px;
}
.header_company > span {
  font-size: 34px;
  margin-top: 2px;
}

.header_info > span {
  margin: 0px 20px;
}

.header_info i {
  margin: 0px 10px;
}

/*          해더           */

.App_body {
  width: 100%;
  margin-top: 100px;
  padding: 54px 188px;
}

.summery {
  color: black;
  font-size: 24px;
}

/*          써머리           */
.filtering {
  display: flex;
}

.filtering button {
  cursor: pointer;
  border: 1px solid #939fa5;
  box-sizing: border-box;
  border-radius: 4px;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 4px 12px;
  background-color: white;
  height: 40px;
  font-size: 19px;
  margin-right: 10px;
}
.filtering button * {
  margin: 0px 10px;
}
.filtering button:hover {
  border: 1px solid #1565c0;
}
.fa-caret-down {
  color: gray;
}
.filtering_togle {
  display: flex;
  flex-direction: row;
  margin-top: 54px;
  justify-content: space-between;
  align-items: center;
}
.filtering_selecter {
  background: #ffffff;
  position: absolute;
  z-index: 1;
  margin-top: 5px;
  display: flex;
  flex-direction: column;
  border: 1px solid #939fa5;
  box-sizing: border-box;
  border-radius: 4px;
}

.filtering_reset {
  cursor: pointer;
  margin-left: 20px;
  font-size: 20px;
  opacity: 0.7;
  color: #1565c0;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.filtering_reset > * {
  margin: 0px 7px;
}
.filtering_reset:hover {
  opacity: 1;
}
.card_empty {
  border: 1px solid gray;
  width: 100%;
  margin-top: 54px;
  height: 120px;
  display: flex;
  user-select: auto;
  justify-content: center;
  align-items: center;
  color: gray;
  opacity: 0.5;
  border-radius: 4px;
}
.card {
  width: 31.3%;
  height: 356px;
  margin-top: 54px;
  background: #ffffff;
  border: 1px solid #e5e5e5;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 24px 16px;
}
.card:hover {
  border: 1px solid #1565c0;
}
.card_form {
  flex-wrap: wrap;
  display: flex;
  justify-content: space-between;
}
.due {
  opacity: 0.5;
  margin: 24px 0px 16px 0px;
}
hr {
  opacity: 0.5;
  margin-bottom: 32px;
}
.count {
  font-weight: bold;
  margin-left: 32px;
}
.answer_btn {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 6px 12px;
  background: #ffffff;
  border-radius: 4px;
  color: #2196f3;
  border: 1px solid #2196f3;
}
.answer_btn:hover {
  color: #ffffff;
  background: #2196f3;
}
.chat_btn {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 6px 12px;
  background: #ffffff;
  border-radius: 4px;
  color: #2196f3;
  border: 1px solid #2196f3;
  margin-left: 5px;
}
.chat_btn:hover {
  color: #ffffff;
  background: #2196f3;
}
.card_btn {
  margin-top: 32px;
  display: flex;
}
.card_title {
  display: flex;
  justify-content: space-between;
}
.chating {
  font-size: 12px;
  font-weight: normal;
  border-radius: 12px;
  padding: 2px 8px;
  border: 1px solid #ffa000;
  color: #ffa000;
  user-select: auto;
  display: flex;
  align-items: center;
}
.mobile_header_info {
  display: none;
}

@media screen and (max-width: 1200px) {
  .card_form {
    display: flex;
    flex-direction: column;
  }
  .card {
    width: 100%;
  }
  .filtering_togle {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .filtering {
    margin-bottom: 10px;
  }
  .header_info {
    display: none;
  }
  .mobile_header_info {
    display: block;
  }
  .fa-bars {
    margin-right: 25px;
  }
  .header {
    padding: 0px 188px;
  }
  .menuwrap {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    overflow: auto;
    width: 80%;
    height: 100%;
    box-sizing: border-box;
    transition: left 0.3s ease-in-out;
    background-color: #ffffff;
  }
  .mobile_company {
    display: flex;
    color: #1565c0;
    padding: 16px 20px;
    align-items: center;
    font-size: 20px;
    box-shadow: 0 1px 4px 0 rgb(30 30 30 / 20%);
  }
  .mobile_info {
    color: #323d45;
    display: flex;
    flex-direction: column;
    padding: 70px 38px;
  }
  .mobile_info > * {
    margin-bottom: 40px;
  }
  .fa-building {
    margin-right: 10px;
  }
  .modal_open {
    position: fixed;
    background-color: black;
    opacity: 0.1;
    width: 100%;
    height: 1000%;
  }
}
